.navigation-wrapper {
  display: flex;
  gap: 1rem;
  height: 4rem;
  position: relative;
  z-index: 1000;
  align-items: center;
  justify-content: space-around;
  align-content: flex-start;
  border-radius: 0 0 10px 10px;
  span {
    a {
      color: #f1f1f1;
      text-decoration: none !important;
      border: none !important;
    }
  }
  .userLogin {
    display: flex;
    align-items: center;
    gap: 1rem;
  }
  .imageUser {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: #f1f1f1;
  }
}
