.layout-wrapper {
  display: grid;
  position: absolute;
  width: 86%;
  height: 85%;
  bottom: 0 !important;
  right: 0;
  background: #efe8e8;
  border-radius: 15px 15px 0px 0px;
  .image {
    width: 11rem;
    height: 9rem;
    display: block;
    position: absolute;
    left: 3rem;
    top: -4rem;
    border-radius: 0 0px 50% 50%;
  }
  .text {
    display: block;
    position: absolute;
    color: #111;
   // width: 73%;
    font-size: 1.4rem;
    font-weight: 500;
    font-family: cursive;
    line-height: 5rem;
    text-align: right;
    right: 1rem;
    span{
        display: block;
    }
  }
}
